@mixin navigation {
    &-list {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 100%;
    }

    &-item {
        list-style: none;
    }

    &-link {
        color: color(primary);
        text-decoration: none;
        font-size: size(sm);
        transition: all .3s;

        &:hover {
            color: lighten(color(primary), 40%);
        }
    }
}

@mixin flexPosition($justCont: center, $alignIte: center) {
    display: flex;
    justify-content: $justCont;
    align-items: $alignIte;
}

@mixin textStyles($transform: uppercase) {
    font-weight: 300;
    letter-spacing: 2px;
    text-transform: $transform;
}

@mixin footerList {
    &-heading {
        font-size: size(lg);
        color: color(secondary);
    }

    &-item {
        list-style: none;
        margin: 1rem 0;
    }

    &-link {
        font-size: size(xs);
        text-decoration: none;
        color: lighten(color(primary), 15%);
        transition: color .2s;

        &:hover {
            color: lighten(color(primary), 35%);
        }
    }
}

@mixin response($breakpoint) {
    @if($breakpoint ==xl) {
        @media(max-width: 1200px) {
            @content;
        }
    }

    @if($breakpoint ==lg) {
        @media(max-width: 1000px) {
            @content;
        }
    }

    @if($breakpoint ==md) {
        @media(max-width: 760px) {
            @content;
        }
    }

    @if($breakpoint ==sm) {
        @media(max-width: 560px) {
            @content;
        }
    }
}