* {
  font-family: 'Josefin Sans', sans-serif;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;

  @include response(md) {
    font-size: 56.25%;
  }

  @include response(sm) {
    font-size: 50%;
  }
}

%fullSpace {
  width: 100%;
  height: 100%;
}

.container {
  display: grid;
  grid-template-columns: minmax(6rem, 1fr) repeat(8, minmax(min-content, 16rem)) minmax(6rem, 1fr);
  grid-template-rows: repeat(4, min-content);
  grid-row-gap: 1.6rem;
  background-color: color(tertiary);
}